.Toolbar {
    height: 56px;
    width: 100%;
    max-width: 1200px;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);

    padding: 20%;
    background-color: #364f6b;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%;
}

@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}